import * as React from 'react';
import Checkbox from '@mui/material/Checkbox';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';

//import FormLabel from '@mui/material/FormLabel';

export default function FormControlLabelPosition(props) {

 

  return (
    <FormControl component="fieldset">
      <FormGroup aria-label="position" row>
        <FormControlLabel
          value={2}
          control={<Checkbox onClick={props.handleOnclick} />}
          label="Température de surface"
          labelPlacement="top"
        />
        <FormControlLabel
          value={1}
          control={<Checkbox onClick={props.handleOnclick} />}
          label="Vent"
          labelPlacement="top"
        />
        <FormControlLabel
          value={2}
          control={<Checkbox onClick={props.handleOnclick}/>}
          label="Température de l'air"
          labelPlacement="top"
        />
        <FormControlLabel
          value="1"
          control={<Checkbox onClick={props.handleOnclick} />}
          label="UTCI"
          labelPlacement="top"
        />
        <FormControlLabel
          value={7}
          control={<Checkbox onClick={props.handleOnclick} />}
          label="Vue Maps"
          labelPlacement="top"
        />
        {/* {showMap && (
          <div className="col-8">
              <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d722.6480003528939!2d1.4793487968871055!3d43.573383991924096!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x12aebc4b08b11265%3A0xa529766154bfd50f!2sPass.%20de%20la%20Piste%20des%20G%C3%A9ants%2C%2031400%20Toulouse!5e0!3m2!1sfr!2sfr!4v1679310353891!5m2!1sfr!2sfr" width="800" 
              height="600"
                style={{border: 0}} 
              allowFullScreen=""
                loading="lazy" 
                referrerPolicy="no-referrer-when-downgrade">
      </iframe>
          </div>
        )} */}
      </FormGroup>

    </FormControl>
  );
}