import T from './T.PNG';
import logo from './logo.svg';
import './App.css';
import NavBarre from './NavBarre';
import SliderSizes from './SliderSizes';
import SliderSizesVegetation from './SliderSizesVegetation';
import FormControlLabelPosition from './FormControlLabelPosition';
import Temperature from './Temperature';
import FormControlLabelPosition3 from './FormControlLabelPosition3';
import FormControlLabelPosition4 from './FormControlLabelPosition4';
import FormControlLabelPosition2 from './FormControlLabelPosition2';
import React, {useState } from 'react';
import data from "./data.json"

//import Page from './Page';

function App() {
  const [SelectedImage, setSelectedImage] = useState(logo);
  const [loader, setLoader] = useState(false);// initialisation du loader à false.
  
  const handleOnclick=(event)=>{
    setLoader(true)// activation du loader
    setTimeout(
      ()=>{
        setSelectedImage(data.filter(obj=>obj.id==event.target.value)[0].img)//Mise à jour de l'image
        setLoader(false) // desactivation du loader
      }, 2000 // attente deux secondes
    )
    
  }  
  const getTimeOfDay = (value) => {
    return value < 12 ? 'am' : 'pm';
  };

  const handleSliderChange = (value) => {
    setLoader(true)
    setTimeout(
      ()=>{
      setSelectedImage(
      value < 12 ? '/static/media/T.1b6480e05a22109cc5d3.PNG': '/static/media/logo.6ce24c58023cc2f8fd88fe9d219db6c6.svg')
      setLoader(false) 
    }, 2000 
    );
  };
  
  return (
  <>
  <NavBarre/>
  <div className="container">
  <div className="row g-2">
    <div className="col-12">
      <div className="p-3 border bg-light">
        <FormControlLabelPosition handleOnclick={handleOnclick}/>
      </div>
    </div>
    <div className="col-8">
      {
        loader?
        <center>
        <div className="spinner-grow text-primary" style={{"width": "16rem", "height": "16rem", "margin-top":"120px"}} role="status">
          <span className="visually-hidden">Loading...</span>
        </div>          
        </center>
        :<img src={SelectedImage} className="img-fluid p-3 border bg-light" alt="..."/>
      }
    </div>
    <div className="col-4">
      <div className="p-3 border bg-light">
        <FormControlLabelPosition3  handleOnclick={handleOnclick}/>
      </div>
      <div className="p-3 border bg-light">
         <SliderSizes onChange={getTimeOfDay} onValueChange={handleSliderChange}  />
         <SliderSizesVegetation onValueChange={handleSliderChange}  />
      </div>
      <div className="p-3 border bg-light">
        <FormControlLabelPosition2 handleOnclick={handleOnclick}/>
      </div>
      <div className="p-3 border bg-light">
        <FormControlLabelPosition4 handleOnclick={handleOnclick}/>
      </div>
      <div className="p-3 border bg-light">
      <Temperature  onValueChange={handleSliderChange} />
      </div>
    </div>
    <div className="col-12">
      <div className="p-3 border bg-light">Custom column padding</div>
    </div>
  </div>
</div>  
  </>
  );
}

export default App;