import * as React from 'react';
import Stack from '@mui/material/Stack';
import Slider from '@mui/material/Slider';
import { Typography } from '@mui/material';
import { Box } from '@mui/system';


function valuetext(value: number) {
  return `${value}°C`;
}

const marks = [
  {
    value: 0,
    label: '0°C',
  },
  {
    value: 20,
    label: '20°C',
  },
  {
    value: 37,
    label: '37°C',
  },
  {
    value: 100,
    label: '100°C',
  },
];

export default function Temperature(props) {

  const[data,setData]= React.useState(0)
  
  const handleValueChange = (value) => {
    props.onValueChange(value); // Call the onValueChange prop with the new value
    setData(value); // Update the state with the new value
  };
  return (
    <Stack sx={{ height: 100 }} spacing={1} direction="row">
       <Box sx={{ display: 'flex', justifyContent: 'center'}}>
      <Typography variant="h8" gutterBottom sx={{textAlign:'center'}}>Capteur de température</Typography>
      <Slider
        getAriaLabel={() => 'Temperature'}
        orientation="vertical"
        getAriaValueText={valuetext}
        //defaultValue={[0, 37]}
        value={data} 
        onChange={(e, value) => handleValueChange(value)} // Call the handleValueChange function on slider change
        valueLabelDisplay="auto"
        marks={marks}
      />
      </Box>
    </Stack>
  );
}